.header-nav {
  & > ul {
    list-style: none;
    display: flex;
    width: 192px;
    justify-content: space-between;
    padding-left: 0;

    & > li > a {
      text-transform: uppercase;
      color: #fefeff;
      font-size: 0.8rem;
      cursor: pointer;
      font-weight: 500;
      position: relative;

      &::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        background-color: #fefeff;
        left: 0;
        bottom: 0;
      }
    }
  }
}

@media screen and (max-width: 480px) {
  .header-nav {
    display: none !important ;
  }
}

@media screen and (max-width: 768px) {
  .header-nav {
    display: none !important ;
  }
}
