.burger-main-menu {
  display: none;
  position: absolute;
  top: 10px;
  left: 10px;
  cursor: pointer;
  font-size: 24px;
  color: #414f6b;
}

.home-page {
  .background-img {
    background-image: url("../../assets/photo-1459666644539-.png");
    height: 100vh;
    width: 100vw;
    background-repeat: no-repeat;
    background-position-x: 55%;
  }

  .header-container {
    display: flex;
    justify-content: space-between;
    padding-top: 8px;
    align-items: center;
  }
}

.fullscreen-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: white;
  z-index: 999;
  transition: opacity 0.3s ease-in-out;
}

.is-open .fullscreen-overlay {
  opacity: 1;
}

.menu-nav {
  margin-top: 50px;
  
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  li {
    margin: 15px 0;
    text-align: center;
  }

  a {
    text-decoration: none;
    color: #333; 
    font-size: 20px; 
    transition: color 0.3s ease-in-out; 
  }

  a:hover {
    color: #007bff; 
  }
}

@media screen and (max-width: 768px) {
  img {
    margin-left: 80px;
  }

  .burger-main-menu {
    display: block;
  }
}

@media screen and (max-width: 480px) {
  img {
    margin-left: 80px;
  }

  .burger-main-menu {
    display: block;
  }
}
