
.carousel-page {
  display: flex;
  flex-direction: column;
  align-items: center;

  .slider {
    width: 100%;
    overflow-x: auto;
    white-space: nowrap;
    scroll-snap-type: x mandatory;
    margin-bottom: 8px;
  }

  .slide {
    display: inline-block;
    flex-shrink: 0;
    scroll-snap-align: start;
    margin-right: 8px;
  }

  img {
    width: 100%;
    max-width: 223px;
    height: auto;
    animation: fadeIn 1s ease-in-out;
  }

  .buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 8px;

    button {
      margin: 0 8px;
    }
  }
}

@media screen and (max-width: 768px) {
  .carousel-page {
    .buttons {
      display: none;
    }

    img {
      margin: 30px 0;
    }
    .slider {
      overflow-x: scroll;
    }

    img {
      max-width: 100%;
    }
  }
}