.burger-menu {
  display: none;
  position: absolute;
  top: 10px;
  left: 10px;
  cursor: pointer;
  font-size: 24px;
  color: #414f6b;
}

.logo-container {
  display: flex;
  justify-content: space-between;
  padding-top: 8px;
  align-items: center;
}

.history-page {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;

  .background-img {
    background-image: url("../../assets/photo-1414449381078-.png");
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .title-container {
      position: relative;
      width: fit-content;

      .pageNb {
        margin-top: 60px;
      }
      .title {
        position: absolute;
        bottom: 18%;
        left: 85%;
      }
    }
  }

  .carousel-background {
    height: 198px;
    width: 100%;
    background-color: RGBA(65, 79, 107, 1);
    opacity: 0.7;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  p {
    margin: 0 70px;
  }

  .fullscreen-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: white;
    z-index: 999;
    transition: opacity 0.3s ease-in-out;
  }

  .is-open .fullscreen-overlay {
    opacity: 1;
  }

  .menu-nav {
    margin-top: 50px;

    ul {
      list-style: none;
      padding: 0;
      margin: 0;
    }

    li {
      margin: 15px 0;
      text-align: center;
    }

    a {
      text-decoration: none;
      color: #333;
      font-size: 20px;
      transition: color 0.3s ease-in-out;
    }

    a:hover {
      color: #007bff;
    }
  }

  @media screen and (max-width: 768px) {
    .history-page .burger-menu {
      .background-img {
        p {
          margin: 0 20px;
        }
      }
    }

    .logo-container{
      margin-left: 90px;
    }

    .header-nav {
      display: block;
    }

    .burger-menu {
      display: block;
    }

    img {
      margin: 0;
    }

    .carousel-background,
    .history-page {
      height: auto;
      overflow: hidden;
    }
  }

  @media screen and (max-width: 480px) {
    .background-img {
      .title-container {
        .title {
          left: 80%;
        }
      }
    }
    .burger-menu {
      display: block;
    }

    img {
      margin: 0;
    }

    overflow: hidden;
  }
}
