.logo-container {
  display: flex;
  justify-content: space-between;
  gap: 90px;
  padding: 29px;
  align-items: center;

  .history-logo {
    position: relative;
    .title {
      position: absolute;
      top: 50%;
      left: 85%;
    }
  }

  .back-arrow {
    width: 30%;
    height: 30%;
    cursor: pointer;
  }
}

.team-page {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;

  .background-img {
    background-image: url("../../assets/photo-1417021423914-\ \(1\).png");
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .schedule-table {
      display: flex;
      flex-direction: column;
      margin: inherit;
      margin-right: 660px;
      margin-top: 100px;
      width: 355px;
      height: 232px;
      background-color: RGBA(255, 255, 255, 1);

      .schedule-table-limiter {
        padding: 23px 41px;

        .schedule-timeline {
          .schedule-timeline-row {
            display: flex;
            justify-content: space-between;

            p {
              margin: 2px 0;
            }

            &:nth-child(3) {
              padding-top: 20px;
            }

            &:nth-child(4) {
              padding-top: 20px;
            }
          }
        }
      }

      h3 {
        color: #414f6b;
        margin: 0;
        font-size: 32px;
      }
    }
  }

  .menu-panel {
    width: 100vw;
    height: 40px;
    background-color: RGBA(65, 79, 107, 1);

    .menu-panel-nav {
      display: flex;
      color: #b0b4be;
    }

    h3 {
      padding: 6px;
      margin: 0;
      color: #b0b4be;
      cursor: pointer;

      &.active {
        background-color: #ccc;
        color: #414f6b;
      }
    }
  }

  .footer {
    background-color: RGBA(65, 79, 107, 1);
    height: 77px;

    .footer-container {
      .footer-logo {
        display: flex;
        align-items: center;
        gap: 10px;
      }
      display: flex;
      justify-content: space-between;
      align-items: center;

      img {
        height: 50px;
        width: 50px;
      }
    }

    h2 {
      color: #63769d;
      font-size: 21px;
      line-height: 20px;
    }

    p {
      color: #63769d;
    }
  }
}

@media screen and (max-width: 768px) {
  .team-page {

    .footer {
      display: none;
    }

    img {
      margin: 0;

      &.title {
        width: 20%;
      }
    }

    p {
      font-size: 0.8rem;
    }

    .background-img {
      .schedule-table {
        margin-right: 0;
        margin-top: 50%;
      }
    }
  }
  .back-arrow {
    display: none;
  }

  .logo-container {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    padding: 15px;
    align-items: center;

    .history-logo {
      position: relative;

      .title {
        position: absolute;
        top: 50%;
        left: 85%;
      }
    }

    .back-arrow {
      display: none;
    }
  }
}

