      }
    }
  }
  .back-arrow {
    display: none;
  }

  .logo-container {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    padding: 15px;
    align-items: center;

    .history-logo {
      position: relative;

      .title {
        position: absolute;
        top: 50%;
        left: 85%;
      }
    }

    .back-arrow {
      display: none;
    }
  }
}
